.chat {
  width: 50%;
  max-width: 550px;
  min-width: 350px;
  padding: 30px;
  position: fixed;
  bottom: 0; }
  .chat .message {
    height: 0;
    opacity: 0;
    overflow: hidden; }
  .chat .text {
    position: relative;
    background-color: #ffffff;
    border-radius: 5px;
    border-bottom-left-radius: 0;
    padding: 10px;
    color: #222222;
    margin: 10px; }
    .chat .text:before {
      content: '';
      display: block;
      position: absolute;
      bottom: 0;
      left: -10px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 0 10px 10px;
      border-color: transparent transparent #ffffff transparent; }
  .chat a {
    font-weight: bold;
    display: block;
    padding: 10px;
    margin: 5px;
    background-color: #DDDDDD;
    border-color: #EEEEEE;
    border-radius: 5px;
    border-width: 1px;
    border-style: solid; }

.spacer {
  height: 500px; }
