nav {
  display: flex;
  width: 100%;
  height: 50px;
  background-image: linear-gradient(to bottom right, #201f1f, #161616);
  justify-content: center;
  box-shadow: #000000 0 0 5px 0;
  z-index: 1; }
  nav a {
    height: 50px;
    text-align: center;
    line-height: 50px;
    color: inherit;
    text-decoration: none;
    padding-left: 20px;
    padding-right: 20px;
    transition: background-color 0.3s ease-in-out; }
    nav a:hover {
      background-color: #ffffff1c; }
    nav a:visited {
      color: inherit; }
    nav a svg,
    nav a span {
      margin-left: 1px;
      margin-right: 1px;
      vertical-align: middle; }
    nav a.active {
      color: #e71616; }
    @media screen and (max-width: 500px) {
      nav a .text {
        display: none; } }
